export const alignItems = [
    { class: "al__it--center", default: false },
    { class: "al__it--start", default: false },
    { class: "al__it--end", default: false },
    { class: "al__it--stretch", default: true },
];
export const alignItemsCodeString = alignItems.map( items => `<div class="flex ${ items.class }">...</div>\n` )
export const justifyItems = [
    { class: 'jf__it--center', default: false },
    { class: 'jf__it--start', default: false },
    { class: 'jf__it--end', default: false },
    { class: 'jf__it--stretch', default: true },
];
export const justifyItemsCodeString = justifyItems.map( items => `<div class="grid ${ items.class }">...</div>\n` )
export const alignContent = [
    { class: "al__cnt--center", default: false },
    { class: "al__cnt--start", default: false },
    { class: "al__cnt--end", default: false },
    { class: "al__cnt--stretch", default: true },
]
export const alignContentCodeString = alignContent.map( items => `<div class="flex ${ items.class }">...</div>\n` )
export const justifyContent = [
    { class: 'jf__cnt--center', default: false },
    { class: 'jf__cnt--start', default: true },
    { class: 'jf__cnt--end', default: false },
    { class: 'jf__cnt--between', default: false },
    { class: 'jf__cnt--around', default: false },
    { class: 'jf__cnt--evenly', default: false },
];
export const justifyContentCodeString = justifyContent.map( items => `<div class="flex ${ items.class }">...</div>\n` )

