import * as React from "react"
import SEO from "../../components/seo";
import { Child, CodeSample, ExampleContainer, SectionWrapper } from "../../components/ExampleComponents";
import { alignItems, alignItemsCodeString, justifyItems, justifyItemsCodeString, alignContent, alignContentCodeString, justifyContent, justifyContentCodeString, alignSelf, alignSelfCodeString, justifySelf, justifySelfCodeString } from "../../data/align-justify";


const AlignJustify = () => {
    return (
        <>
            <SEO title={ "Align / Justify" }/>
            <h1>Align / Justify</h1>
            <p>Align and Justify can be used for both Grid and Flexbox.</p>
            <p>The class constructor looks like this:</p>
            <CodeSample label={ "For Align" } codeString={ `al__{type}--{value}` }/>
            <CodeSample label={ "For Justify" } codeString={ `jf__{type}--{value}` }/>

            <SectionWrapper id={ "align-items" } codeString={ alignItemsCodeString.join( '' ) }>
                { alignItems.map( ( items, index ) => {
                    return (
                        <ExampleContainer key={ index } className={ items.class } isDefault={ items.default }>
                            <Child fullwidth/>
                            <Child fullwidth/>
                            <Child fullwidth/>
                        </ExampleContainer>
                    )
                } ) }
            </SectionWrapper>

            <SectionWrapper id={ "justify-items" } codeString={ justifyItemsCodeString.join( '' ) }>
                { justifyItems.map( ( items, index ) => {
                    return (
                        <ExampleContainer key={ index } grid className={ items.class } isDefault={ items.default } useHeight={ false }>
                            <Child/>
                            <Child/>
                            <Child/>
                        </ExampleContainer>
                    )
                } ) }
            </SectionWrapper>

            <SectionWrapper id={ "align-content" } codeString={ alignContentCodeString.join( '' ) }>
                { alignContent.map( ( items, index ) => {
                    return (
                        <ExampleContainer key={ index } className={ items.class } isDefault={ items.default } height={ 200 } wrap>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                            <Child/>
                        </ExampleContainer>
                    )
                } ) }
            </SectionWrapper>

            <SectionWrapper id={ "justify-content" } codeString={ justifyContentCodeString.join( '' ) }>
                { justifyContent.map( ( items, index ) => {
                    return (
                        <ExampleContainer key={ index } className={ items.class } isDefault={ items.default } useHeight={ false }>
                            <Child/>
                            <Child/>
                            <Child/>
                        </ExampleContainer>
                    )
                } ) }
            </SectionWrapper>

            <SectionWrapper id={ "align-self" } codeString={ `<div class="grid">\n${ alignSelfCodeString.join( '' ) }</div>` }>
                <ExampleContainer>
                    { alignSelf.map( ( items, index ) => {
                        return (<Child key={ index } className={ items.class } children={ items.class } fullwidth/>)
                    } ) }
                </ExampleContainer>
            </SectionWrapper>

            <SectionWrapper id={ "justify-self" } codeString={ `<div class="grid">\n${ justifySelfCodeString.join( '' ) }</div>` }>
                <ExampleContainer useHeight={ false } wrap grid>
                    { justifySelf.map( ( items, index ) => {
                        return (<Child key={ items.class } className={ items.class } children={ items.class }/>)
                    } ) }
                </ExampleContainer>
            </SectionWrapper>
        </>
    )
}

export default AlignJustify;


